<template>
  <div class="graph">
    <canvas :id="'planet-chart' + id"></canvas>
  </div>
</template>

<script>
import Chart from "chart.js";
// import planetChartData from "./planet-data.js";

export default {
  name: "PlanetChart",
  props: {
    data: {
      type: Object,
    },
    nameX: String,
    nameY: String,
    id: Number,
  },
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  data() {
    return {
      options: {
        plugins: {
          legend: {
            title: {
              display: true,
              text: "Legend Title",
            },
          },
        },
        responsive: true,
        lineTension: 1,
        scales: {
          xAxes: [
            {
              type: "time",
              display: true,
              position: "bottom",
              id: "xAxeTime",
              scaleLabel: {
                display: true,
                labelString: this.nameX,
                fontColor: "black",
              },
              time: {
                unit: "month",
                // parser: "moment.ISO_8601", // -> remove this line
                tooltipFormat: "ll",
              },
            },
          ],
          yAxes: [
            {
              type: "linear",
              display: true,
              position: "left",
              id: "yAxeTemperature",
              scaleLabel: {
                display: true,
                labelString: this.nameY,
                fontColor: "red",
              },
            },
          ],
        },
      },
    };
  },
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  mounted() {
    const ctx = document.getElementById("planet-chart" + this.id);
    // console.log(this.data);
    new Chart(ctx, {
      type: "scatter",
      data: this.data,
      options: this.options,
    });
  },
};
</script>
